<template lang="html">
  <div class="pos">
    <el-row>
      <el-col :span = '7' class="pos-order" id="order-list">
        <el-tabs>
          <el-tab-pane label='点餐'>
            <el-table :data='tableData' border style="width:100%">
              <el-table-column prop='goodsName' label='商品名称'></el-table-column>
              <el-table-column prop='count' label='数量' width='50'></el-table-column>
              <el-table-column prop='price' label='金额' width='70'></el-table-column>
              <el-table-column label='操作' fixed = 'right'>
                <template scope = 'scope'> <!-- 看不懂 -->
                  <el-button type='text' size ='small' @click='removeOrderList(scope.row)'>删除</el-button>
                  <el-button type='text' size ='small' @click='addOrderList(scope.row)'>增加</el-button>
                </template>
              </el-table-column>

            </el-table>
            <div class="">
              <small>数量:</small><span style="padding-right:30px">{{totalCount}}</span><small>金额:</small><span>{{totalMoney}}</span>
            </div>
            <div class='div-btn'>
              <el-button type='warning'>挂单</el-button>
              <el-button type='danger' @click='removAllOrder'>删除</el-button>
              <el-button type='success' @click='checkout'>结账</el-button>
            </div>
          </el-tab-pane>
          <el-tab-pane label='挂单'>

          </el-tab-pane>
          <el-tab-pane label='外卖'>

          </el-tab-pane>
        </el-tabs>
      </el-col>
      <el-col :span='17'>
        <div class="often-goods">
          <div class="title"> 常用商品 </div>
        </div>
        <div class="often-goods-list">
          <ul>
            <li v-for='goods in oftenGoods' @click='addOrderList(goods)'>
              <span>{{goods.goodsName}}</span>
              <span class="o-price">￥{{goods.price}}元</span>
            </li>
          </ul>
        </div>

        <div class="goods-type">
          <el-tabs>
            <el-tab-pane label='汉堡' >
              <div>
                <ul class='cookList'>
                  <li v-for='goods in type0Goods' @click='addOrderList(goods)'>
                      <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                      <span class="foodName">{{goods.goodsName}}</span>
                      <span class="foodPrice">￥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label='小食' >
              <div>
                <ul class='cookList'>
                  <li v-for='goods in type1Goods' @click='addOrderList(goods)'>
                      <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                      <span class="foodName">{{goods.goodsName}}</span>
                      <span class="foodPrice">￥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label='饮料' >
              <div>
                <ul class='cookList'>
                  <li v-for='goods in type2Goods' @click='addOrderList(goods)'>
                      <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                      <span class="foodName">{{goods.goodsName}}</span>
                      <span class="foodPrice">￥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
            <el-tab-pane label='套餐' >
              <div>
                <ul class='cookList'>
                  <li v-for='goods in type3Goods'>
                      <span class="foodImg"><img :src="goods.goodsImg" width="100%"></span>
                      <span class="foodName">{{goods.goodsName}}</span>
                      <span class="foodPrice">￥{{goods.price}}元</span>
                  </li>
                </ul>
              </div>
            </el-tab-pane>
          </el-tabs>
        </div>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from 'axios'
export default {
  name:'pos',
  data(){
    return {
      tableData:[

      ],
      oftenGoods:[

      ],
      type0Goods:[

      ],
      type1Goods:[

      ],
      type2Goods:[

      ],
      type3Goods:[

      ],
      totalMoney:0,
      totalCount:0
    }
  },
  methods:{
    addOrderList(goods){
      this.totalCount=0
      this.totalMoney=0
      //这个商品是否已经存在列表单中
      let isHave = false
      for(let i=0;i<this.tableData.length;i++){
        if(this.tableData[i].goodsId==goods.goodsId){
          isHave=true
        }
      }
      //根据判断的编写业务逻辑
      if(isHave){
        //改变列表中商品数量
        let arr = this.tableData.filter(o=>o.goodsId==goods.goodsId)
        arr[0].count++
      }else {
        let newGoods ={goodsId:goods.goodsId,goodsName:goods.goodsName,price:goods.price,count:1}
        this.tableData.push(newGoods)
      }
      this.getAllMoney()

    },
    removeOrderList(goods){
      this.tableData=this.tableData.filter(o=>o.goodsId != goods.goodsId)
      this.getAllMoney()
    },
    getAllMoney(){
      this.totalCount=0;
      this.totalMoney=0;
      if (this.tableData) {
        this.tableData.forEach((element)=>{
          this.totalCount+=element.count
          this.totalMoney=this.totalMoney+element.price*element.count
        })
      }
    },
    removAllOrder(){
      this.totalCount=0;
      this.totalMoney=0;
      this.tableData=[]
    },
    checkout(){
      if(this.tableData[0]){
        this.totalCount=0;
        this.totalMoney=0;
        this.tableData=[];
        this.$message({
          message:'结账成功，感谢您本次的消费',
          type:'success'
        })

      }else {
        this.$message.error('请先选择,或者过于频繁点击')
      }
    }
  },
  created:function(){
    axios.get('http://jspang.com/DemoApi/oftenGoods.php')
    .then(response=>{
      this.oftenGoods=response.data
    })
    .catch(error=>{
      alert('网络错误，无法访问')
    })

    axios.get('http://jspang.com/DemoApi/typeGoods.php')
    .then(response=>{
      this.type0Goods=response.data[0]
      this.type1Goods=response.data[1]
      this.type2Goods=response.data[2]
      this.type3Goods=response.data[3]
    })
    .catch(error=>{
      alert('网络错误，无法访问')
    })

  },
  mounted:function(){
    var orderHeight = document.body.clientHeight;
    var orderList = document.getElementById('order-list').style.height = orderHeight+'px'

  }
}
</script>

<style scoped>
.pos-order{
  background-color: #F9FAFC;
  border-right: ipx solid #C0CCDA
}
.div-btn{
  margin-top:10px
}
.title{
  height:20px;
  border-bottom: 1px solid #D3dce6;
  background-color: #F9FAFC;
  padding: 10px;
  text-align: left

}
.often-goods-list ul{
  perspective: 1000px
}
.often-goods-list ul li{
  list-style: none;
  float: left;
  border: 1px solid #F9FAFC;
  padding: 10px;
  margin: 10px;
  background-color: #FFF;
  cursor: pointer;
}
.often-goods-list ul li:hover{
  box-shadow: 0px 0px 25px #888888;
  transform: translateZ(20px);
}
.o-price{
  color: #58B7FF
}
.goods-type{
  clear: both;
}
.cookList li{
       list-style: none;
       width:23%;
       border:1px solid #E5E9F2;
       height: auot;
       overflow: hidden;
       background-color:#fff;
       padding: 2px;
       float:left;
       margin: 2px;
       cursor: pointer;
   }
   .cookList li span{

        display: block;
        float:left;
   }
   .foodImg{
       width: 40%;
   }
   .foodName{
       font-size: 18px;
       padding-left: 10px;
       color:brown;

   }
  .foodPrice{
       font-size: 16px;
       padding-left: 10px;
       padding-top:10px;
   }

</style>



